html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 1.6rem;
  line-height: 2;
  margin: 0;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3 {
  font-family: "Raleway", sans-serif;
  line-height: 1.5;
  margin: 0;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
}

.header {
  background-image: url("../../../build/img/background@2x.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.header__content {
  padding-bottom: 10rem;
}
.header__logo img {
  margin: 0 auto;
  padding-top: 2rem;
}
.header__contenido {
  margin-top: 5.4rem;
  display: grid;
}
@media (min-width: 1024px) {
  .header__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.header__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header__titulo {
  font-size: 1.6rem;
  font-weight: 300;
  text-align: center;
  color: #36414d;
  line-height: 7.5rem;
}
@media (min-width: 480px) {
  .header__titulo {
    font-size: 2.4rem;
  }
}
@media (min-width: 768px) {
  .header__titulo {
    font-size: 3.4rem;
  }
}
@media (min-width: 768px) {
  .header__titulo::after {
    content: "";
    position: relative;
    right: 123px;
    width: 115%;
    height: 0.1rem;
    display: block;
    background-color: #16161758;
    margin-top: 1.5rem;
  }
}
.header__titulo::after {
  content: "";
  position: relative;
  right: 123px;
  width: 115%;
  height: 0.1rem;
  display: block;
  background-color: #16161758;
  margin-top: 1.5rem;
}
.header__span {
  font-size: 3.3rem;
  font-weight: bold;
  display: block;
}
@media (min-width: 768px) {
  .header__span {
    font-size: 5.3rem;
    font-size: 4.3rem;
  }
}
.header__text {
  font-size: 1.6rem;
  text-align: center;
  line-height: 3.2rem;
  color: #000001;
  margin-top: 2.5rem;
  font-weight: 600;
}
@media (min-width: 480px) {
  .header__text {
    font-size: 1.8rem;
  }
}
.header__marcas {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
}
.header__marcas img {
  width: 15rem;
}
@media (min-width: 768px) {
  .header__marcas img {
    width: 20rem;
  }
}
.header__marcas img:nth-child(3) {
  grid-column: 1/3;
  margin: 0 auto;
}

.form {
  margin-top: 5rem;
  border-radius: 0.5rem;
  justify-self: center;
}
@media (min-width: 1024px) {
  .form {
    margin-top: unset;
  }
}
.form__titulo {
  font-weight: 900;
  text-align: center;
  font-size: 2.5rem;
  color: #000001;
  line-height: 3.6rem;
  margin-bottom: 1.8rem;
  margin-right: 3rem;
}
@media (min-width: 768px) {
  .form__titulo {
    font-size: 2.9rem;
    margin-left: unset;
    width: 39.5rem;
    margin-right: unset;
  }
}
.form__formulario {
  background-color: #FFFFFF;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .form__formulario {
    margin: unset;
  }
}
.form__campo {
  padding: 1rem 5rem;
}
.form__fname {
  margin-top: 2.8rem;
}
.form__label {
  display: block;
  font-size: 1.6rem;
  font-weight: bold;
  color: #36414d;
  margin-bottom: 1rem;
}
.form__input, .form__select {
  width: 100%;
  border: 1px solid #a4a89d;
  padding: 0.5rem;
  border-radius: 0.3rem;
  outline: none;
}
.form__input::placeholder {
  color: #95989A;
  font-size: 1.7rem;
  padding: 1rem;
}
.form__select {
  color: #95989A;
  font-size: 1.7rem;
  padding: 0.5rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.form__poligono::after {
  content: "";
  display: block;
  width: 1rem;
  height: 1rem;
  background-image: url(../../../build/img/PoliÃÅgono\ 1@2x.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  top: -20px;
  left: 245px;
  z-index: 1000;
}
@media (min-width: 768px) {
  .form__poligono::after {
    left: 280px;
  }
}
.form__botton {
  background-color: #a8895b;
  border: 1px solid #95989A;
  cursor: pointer;
  color: #FFFFFF;
  display: inline-block;
  font-weight: 900;
  font-size: 1.7rem;
  margin-top: 1rem;
  padding: 1rem 2rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 16rem;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}
.form__botton:hover {
  background-color: #886e48;
}
.form__submit {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__input-error {
  margin-top: 1rem;
  font-size: 1.6rem;
  line-height: 1.2;
  margin-bottom: 1rem;
  display: none;
  color: #FFFFFF;
  background-color: #bb2929;
  padding: 1rem;
}

.form__input-error-activo {
  display: block;
}

.form__incorrecto {
  border: 1px solid #bb2929;
}

.warning {
  margin-top: 1rem;
  font-size: 1.6rem;
  line-height: 1.2;
  margin-bottom: 1rem;
  color: #FFFFFF;
  background-color: #bb2929;
  padding: 2rem;
  text-align: center;
}

.exito {
  margin-top: 1rem;
  font-size: 1.6rem;
  line-height: 1.2;
  margin-bottom: 1rem;
  color: #FFFFFF;
  background-color: #a8895b;
  padding: 2rem;
  text-align: center;
}

.banner {
  height: 36.2rem;
  background-image: url("../../../build/img/picture-01@2x.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.banner__info {
  width: 91.9rem;
}
.banner__text {
  text-align: center;
  font-size: 3rem;
  line-height: 6rem;
  font-weight: 500;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .banner__text {
    font-size: 4rem;
  }
}

.main {
  background-color: #fafafa;
}

.program__container {
  padding: 5.4rem 0;
}
@media (min-width: 768px) {
  .program__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.program__foto {
  display: flex;
  justify-content: center;
}
.program__head {
  margin-top: 5rem;
}
.program__head h3 {
  font-size: 2.2rem;
  font-weight: 800;
  color: #36414d;
}
@media (min-width: 480px) {
  .program__head h3 {
    font-size: 2.9rem;
  }
}
.program__head p {
  font-size: 1.4rem;
  color: #36414d;
  font-weight: 500;
  line-height: 3.2rem;
}
@media (min-width: 480px) {
  .program__head p {
    font-size: 1.8rem;
  }
}
.program__content {
  margin-top: 3rem;
}
.program__content h3 {
  font-size: 2.2rem;
  font-weight: 500;
  color: #36414d;
}
.program__li p {
  font-size: 2rem;
  font-weight: 700;
  color: #a8895b;
  letter-spacing: 0.2rem;
  margin-top: 1.5rem;
  margin-left: 2rem;
}
.program__li::before {
  content: "";
  display: block;
  width: 1rem;
  height: 1rem;
  background-image: url(../../../build/img/check-icon@2x.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  top: 45px;
  z-index: 1000;
}

.student__titulo__head {
  margin-top: 5rem;
  text-align: center;
  font-size: 3.2rem;
  color: #36414d;
  font-weight: 500;
}
.student__contenido {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .student__contenido {
    flex-direction: row;
  }
}
.student__card {
  margin-top: 5rem;
  flex-basis: calc(25.5% - 1rem);
}
.student__titulo {
  font-size: 2.3rem;
  text-align: center;
  color: #36414d;
  font-weight: 700;
  margin: 1.6rem 0;
}
.student__info {
  font-size: 1.4rem;
  color: #36414d;
  font-weight: 400;
  text-align: center;
}

.contacto {
  background-color: #fafafa;
}
.contacto__contenedor {
  display: grid;
  gap: 5rem;
  margin-top: 4rem;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .contacto__contenedor {
    grid-template-columns: repeat(2, 1fr);
    margin-top: 6rem;
  }
}
.contacto__imagen {
  height: 51.2rem;
  justify-self: center;
}
@media (min-width: 768px) {
  .contacto__imagen {
    width: 40.8rem;
    justify-self: center;
  }
}
.contacto__titulo {
  font-size: 2.9rem;
  color: #36414d;
  font-weight: 800;
  margin-top: 0;
  margin-bottom: 3rem;
}
.contacto__superior p {
  font-size: 1.8rem;
  color: #36414d;
  line-height: 2.6rem;
  margin-bottom: 2rem;
}
.contacto__span {
  font-weight: 700;
}
.contacto__subtitulo {
  color: #36414d;
  font-size: 2.2rem;
  font-weight: 800;
  margin: 3rem 0 2rem 0;
}
.contacto__medio {
  margin-bottom: 2rem;
}
.contacto__medio p {
  font-size: 1.8rem;
  color: #36414d;
  font-weight: 500;
}
.contacto__inferiror {
  display: flex;
  gap: 1rem;
}
.contacto__inferiror img {
  width: 15.9rem;
}

.footer__contenido {
  padding: 3rem 0;
  display: grid;
}
@media (min-width: 768px) {
  .footer__contenido {
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__comun {
  justify-self: center;
  color: #36414d;
  font-size: 1.6rem;
  font-weight: 400;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .footer__comun {
    margin-top: unset;
  }
}
.footer__redes {
  display: flex;
  gap: 3rem;
}
.footer__redes img {
  width: 3.4rem;
  height: 3.4rem;
}
.footer__span {
  margin-left: 2rem;
}